
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

export default{
directives: {
TransferDom
},
components: {
Tab,
TabItem,
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
},
data(){
return{
allmoney:0,
getBarWidth: function (index) {
return (index + 1) * 22 + 'px'
},
index:this.$route.params.id,
mz:'',
address:'',
telphone:''
}
},
mounted() {
var i=0
while(i<this.$store.state.admin.order.length){
this.allmoney+=this.$store.state.admin.order[i].num*this.$store.state.admin.order[i].pay
i++
}
},
methods:{
zfb(){
if(this.$store.state.admin.dz&&this.$store.state.admin.mz){
this.$store.state.admin.finshorder.push(JSON.parse(JSON.stringify(this.$store.state.admin.order[this.index])))
alert('支付成功！')
this.$router.push('/Allbuy')
}else{
alert("请填写完整个人信息！")
}

},
cilck1(){
alert(1)
},
onItemClick (index) {
console.log('on item click:', index)
},
gb(){
this.$store.state.admin.dz=this.address
},
xm(){

this.$store.state.admin.mz=this.mz
},
tel(){
this.$store.state.admin.tel=this.telphone
}
}
}



