  import FootEnd from './components/footer.vue'
  import '../static/style.css'
export default {
    data(){
      return{
        animate:'animate'
      }
    },
  components:{FootEnd},
  watch:{
      $route(to,from){
        if(from.path=="/shopPage/0"&&to.path=="/"||from.path=="/buyNow/0"||from.path=="/me"||from.path=="/Allbuy"&&to.path=="/"){
          this.animate="animate2"
        }else{
          this.animate="animate"
        }
    }
  }
}



